@import url(https://fonts.googleapis.com/css2?family=Arima+Madurai:wght@700&family=Mali&family=Martel+Sans:wght@300&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mali&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mali&family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
  background-color: #7D8597;
  background-image: url(/static/media/cardboard.490a0a58.png);
}

h1.title{
  color: #001845 ;
  font-family: 'Arima Madurai';
  font-size: 5vh;
  padding-left: 2vh;
  padding-bottom: 0;
  margin-bottom: 0;
  margin-top: 0;
}

p{
  margin-top: 0;
  padding-left: 4vh;
  color: #001845;
  font-size: 130%;
  font-family: 'Martel Sans', sans-serif;
}

section.text-block{
  display: flex;
  align-items: flex-start;
}

div.text-container{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 70%;
  margin-right: 5%;
}
.header-h1{
  font-family: 'Russo One', sans-serif;
  font-size: 300%;
  text-align: center;
  color: #001233;
  text-shadow: 2px 0 0 #0353A4, -2px 0 0 #0353A4, 0 2px 0 #0353A4, 0 -2px 0 #0353A4
  
}

.portifolio-header{
  display: flex;
  flex-direction: column;
  background-color: #001845;
  background-image: url(/static/media/Texture-Circuit-Board-Pcb-Background-Wallpaper-5962474.21435df7.png);
  margin-bottom: 2vh;

}

.header-nav{
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  text-align: center;
}

.nav-button{
  background-color: #00123391;
  flex-grow: 1;
  border:dotted black 1px;
  padding: 1vh;
  text-decoration: none;
  font-family: 'Mali', cursive;
  color:#0353A4;
  font-size: 2.5vh;
}

.nav-button:hover{
  background-color: #0353A4;
  color:#00123391;
}


.footer-nav{
  display: flex;
  justify-content: space-around;
}

.footer-nav a{
  display: flex;
  align-items: center;
}

.footer-icon{
  width: 20vh;
}

.footer-icon:hover{
  opacity: 0.8;
}

.portfolio-footer{
  background-color: #0353A4;
  background-image: url(/static/media/45-degree-fabric-dark.3a1471f2.png);
}
img.minha-foto{
  width: 30vh;
  margin: 0vh 0vh 5vh 5vh;
  object-fit: contain;
  border-radius: 10px;
  border: solid 1px #001845;;
}

img.keyboard{
  width: 15%;

  -webkit-animation: rotate 10s linear infinite;

          animation: rotate 10s linear infinite;
  align-self: center;
  justify-self: flex-start;
  

}

@-webkit-keyframes rotate{
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes rotate{
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}


.portfolio-title{
  
  padding-right: 1%;
  color: #002855;
  font-family:'Russo One';
}

.title-container{
  background-color: #0353A4;
  justify-content: center;
}

.portfolio-title-img{
  height: 4vh;
}

.title-container{
  display: flex;
  align-items: center;
}

